.Outer{
  width: 100%;
  background-size: cover;
  background-attachment: fixed;
  padding: 100px;
  //border: solid black;
}
.fadeUp {
  -webkit-animation-name: fadeUp;
          animation-name: fadeUp;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
  opacity: 1;
}


.Jumbo{
  //border: solid black;
  background-color: white;
  min-height: 600px;
  opacity: 0;
  padding-top: 50px;
  padding-bottom: 50px;
  margin-top: 50px;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Hover-Word {
  opacity: 1;
  color: black;
  z-index: 6;
  font-size: 3em;
  border-style: solid;

  position: absolute;

  max-width: 300px;
  min-width: 300px;
  margin-top: 210px;
  margin-left: 330px;
  font-family: sans-serif;
  font-stretch: expanded;
  font-weight: lighter;
  text-align: center;


  background-position: -0% 0;
  background-size: 200% auto;
  background-image: -webkit-gradient(linear,left top, right top,color-stop(50%, rgba(1,1,1,0.0)), color-stop(50%, rgba(1,1,1,0.4)) );
  background-image: -webkit-linear-gradient(left,rgba(1,1,1,0.0) 50%, rgba(1,1,1,0.4) 50% );
  background-image: -o-linear-gradient(left,rgba(1,1,1,0.0) 50%, rgba(1,1,1,0.4) 50% );
  background-image: linear-gradient(to right,rgba(1,1,1,0.0) 50%, rgba(1,1,1,0.4) 50% );
  -webkit-transition: background 1.0s ease-out;
  -o-transition: background 1.0s ease-out;
  transition: background 1.0s ease-out;

  padding:15px;
  overflow: hidden;


}
.Hover-Word:hover{
  opacity: 1;
  color: whitesmoke;
  background-position: -99.99% 0;
}

img{
  max-width: 950px;
  max-height: 540px;
}



