.NavMain{
  z-index: 5;
  border: solid 0.5px;
  background: rgba(255,255,255,0.96);
  -webkit-transition: -webkit-transform 400ms ease-in;
  transition: -webkit-transform 400ms ease-in;
  -o-transition: transform 400ms ease-in;
  transition: transform 400ms ease-in;
  transition: transform 400ms ease-in, -webkit-transform 400ms ease-in;
  font-size: 1.2em;

}

.slide-up{
  -webkit-animation: slide-up 0.5s ease-in forwards;
          animation: slide-up 0.5s ease-in forwards;
}

@-webkit-keyframes slide-up {
  0% { -webkit-transform: translateY(0%); transform: translateY(0%); }
  100% { -webkit-transform: translateY(-100%); transform: translateY(-100%); }
}

@keyframes slide-up {
  0% { -webkit-transform: translateY(0%); transform: translateY(0%); }
  100% { -webkit-transform: translateY(-100%); transform: translateY(-100%); }
}

.slide-down{
  -webkit-animation: slide-down 0.5s ease-in forwards;
          animation: slide-down 0.5s ease-in forwards;
}

@-webkit-keyframes slide-down {
  0% { -webkit-transform: translateY(-100%); transform: translateY(-100%); }
  100% { -webkit-transform: translateY(0%); transform: translateY(0%); }
}

@keyframes slide-down {
  0% { -webkit-transform: translateY(-100%); transform: translateY(-100%); }
  100% { -webkit-transform: translateY(0%); transform: translateY(0%); }
}

.slideNav{
  -webkit-animation: slideNav 0.2s ease forwards;
          animation: slideNav 0.2s ease forwards;
}

.Nav-Btn{
  cursor: pointer;
  width:10em;
  margin:0 auto;
  z-index: 10;
  position: fixed;
  left: 48.9%;
  top: 80px;
  height: inherit;
}

.Top-Bar,.Mid-Bar,.Bot-Bar{
  height:2px;
  width: 2.0em;
  display:block;
  margin-bottom: 3px;
  background: #B5B4B4;;
}

@-webkit-keyframes slideNav {
  0% { -webkit-transform: translateY(0%); transform: translateY(0%); }
  100% { -webkit-transform: translateY(-100%); transform: translateY(-100%); }
}

@keyframes slideNav {
  0% { -webkit-transform: translateY(0%); transform: translateY(0%); }
  100% { -webkit-transform: translateY(-100%); transform: translateY(-100%); }
}

.underline{
    font-weight: lighter;

    -webkit-transform: perspective(1px) translateZ(0);

            transform: perspective(1px) translateZ(0);
    -webkit-box-shadow: 0 0 1px transparent;
            box-shadow: 0 0 1px transparent;
    position: relative;
    overflow: hidden;
}
.underline:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0%;
    background: black;
    top:50px;

    height:2.5px;

    -webkit-transition-property: right;

    -o-transition-property: right;

    transition-property: right;

    -webkit-transition-duration: 0.3s;

         -o-transition-duration: 0.3s;

            transition-duration: 0.3s;

    -webkit-transition-timing-function: ease-out;

         -o-transition-timing-function: ease-out;

            transition-timing-function: ease-out;
}


.underline:hover:before, .hvr-underline-from-left:focus:before, .hvr-underline-from- left:active:before {
    right: 0;
}



