.Welcome{
  text-align: center;
  background-color: white;
  z-index: -3;
  height: 50em;
}

.fly-in-text {
    z-index: 4;
    list-style: none;
    position: relative;
    left: 72%;
    top: 48%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    min-width: 100%;
    overflow-x: hidden;
}

.fly-in-text li {
    display: inline-block;
    margin-right:1em;
    font-family: sans-serif;
    font-weight: lighter;
    font-size: 3.5em;
    color:white;
    opacity: 0;
    -webkit-transition: all 2.5s ease;
    -o-transition: all 2.5s ease;
    transition: all 2.5s ease;
}

.fly-in-text li:nth-child(1) { -webkit-animation: fly-in-1 2.5s ease; animation: fly-in-1 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}
.fly-in-text li:nth-child(2) { -webkit-animation: fly-in-2 2.5s ease; animation: fly-in-2 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}
.fly-in-text li:nth-child(3) { -webkit-animation: fly-in-3 2.5s ease; animation: fly-in-3 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}
.fly-in-text li:nth-child(4) { -webkit-animation: fly-in-4 2.5s ease; animation: fly-in-4 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}
.fly-in-text li:nth-child(5) { -webkit-animation: fly-in-5 2.5s ease; animation: fly-in-5 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}
.fly-in-text li:nth-child(6) { -webkit-animation: fly-in-6 2.5s ease; animation: fly-in-6 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}
.fly-in-text li:nth-child(7) { -webkit-animation: fly-in-7 2.5s ease; animation: fly-in-7 2.5s ease; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; -webkit-animation-fill-mode: forwards; animation-fill-mode: forwards;}

.fly-in-text li:first-child{ margin-left: 0.65em; }

.fly-in-text li:last-child { margin-right: 0; }

.Slide-Box {
  background-color: #222;
  z-index: 4;
  height: inherit;
  color: black;
  overflow-y: hidden;
  overflow-x: hidden;
}

.red-accent{
  color:#F94E4E;
  font-style: italic;
  left:90%;
}

.my-Name{
  color:#494949;
  position: relative;
  left: 0%;
  top: -55%;
  width: 35%;
  border-style: solid;
  border-color:#F6F5F5;
  border-width: thin;
  height: 3em;
  width: 48%;
  z-index: 0;
  font-size: 1em;

  overflow: hidden;
  text-align: center;
  line-height:2.5em;
  float: left;

  opacity: 0;
  font-family:sans-serif;
  background: white;


  -webkit-box-shadow:20px 20px 15px #F6F5F5;


          box-shadow:20px 20px 15px #F6F5F5;
}

.slide{
  -webkit-animation: slide 2s ease-in forwards;
          animation: slide 2s ease-in forwards;
}

.slide-out{
  -webkit-animation: slide-out 2.7s ease-in forwards;
          animation: slide-out 2.7s ease-in forwards;

}

@-webkit-keyframes slide-out{
  0% { -webkit-transform: translateX(80%); transform: translateX(80%); opacity: 0; z-index: -1}

  100% { -webkit-transform: translateX(106%); transform: translateX(106%); opacity: 1; z-index: 1}
}

@keyframes slide-out{
  0% { -webkit-transform: translateX(80%); transform: translateX(80%); opacity: 0; z-index: -1}

  100% { -webkit-transform: translateX(106%); transform: translateX(106%); opacity: 1; z-index: 1}
}

@-webkit-keyframes slide {
  0% { -webkit-transform: translateX(0%); transform: translateX(0%); }
  100% { -webkit-transform: translateX(-50%); transform: translateX(-50%); }
}

@keyframes slide {
  0% { -webkit-transform: translateX(0%); transform: translateX(0%); }
  100% { -webkit-transform: translateX(-50%); transform: translateX(-50%); }
}

@-webkit-keyframes fly-in-1{
  0% { -webkit-transform: translateX(-200px) translateY(-200px); transform: translateX(-200px) translateY(-200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}

@keyframes fly-in-1{
  0% { -webkit-transform: translateX(-200px) translateY(-200px); transform: translateX(-200px) translateY(-200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@-webkit-keyframes fly-in-2{
  0% { -webkit-transform: translateX(20px) translateY(100px); transform: translateX(20px) translateY(100px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@keyframes fly-in-2{
  0% { -webkit-transform: translateX(20px) translateY(100px); transform: translateX(20px) translateY(100px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@-webkit-keyframes fly-in-3{
  0% { -webkit-transform: translateX(-150px) translateY(-80px); transform: translateX(-150px) translateY(-80px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@keyframes fly-in-3{
  0% { -webkit-transform: translateX(-150px) translateY(-80px); transform: translateX(-150px) translateY(-80px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@-webkit-keyframes fly-in-4{
  0% { -webkit-transform: translateX(10px) translateY(-200px); transform: translateX(10px) translateY(-200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@keyframes fly-in-4{
  0% { -webkit-transform: translateX(10px) translateY(-200px); transform: translateX(10px) translateY(-200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@-webkit-keyframes fly-in-5{
  0% { -webkit-transform: translateX(-300px) translateY(200px); transform: translateX(-300px) translateY(200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@keyframes fly-in-5{
  0% { -webkit-transform: translateX(-300px) translateY(200px); transform: translateX(-300px) translateY(200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@-webkit-keyframes fly-in-6{
  0% { -webkit-transform: translateX(20px) translateY(-20px); transform: translateX(20px) translateY(-20px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@keyframes fly-in-6{
  0% { -webkit-transform: translateX(20px) translateY(-20px); transform: translateX(20px) translateY(-20px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@-webkit-keyframes fly-in-7{
  0% { -webkit-transform: translateX(30px) translateY(200px); transform: translateX(30px) translateY(200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}
@keyframes fly-in-7{
  0% { -webkit-transform: translateX(30px) translateY(200px); transform: translateX(30px) translateY(200px); opacity: 0; }
  100% { -webkit-transform: translateX(0px) translateY(0px); transform: translateX(0px) translateY(0px); opacity: 1; }
}



